import { Component, Host, Listen, h } from '@stencil/core';

/**
 * @slot content - For the snack bar stack conent
 */
@Component({
  tag: 'mdx-snack-bar-stack',
  styleUrl: 'snack-bar-stack.scss',
  shadow: true,
})
export class SnackBarStack {
  @Listen('close', { target: 'body' })
  toggle({ target }) {
    target.remove();
  }

  render() {
    return (
      <Host>
        <slot />
      </Host>
    );
  }
}
